@import url("https://fonts.googleapis.com/css?family=Inconsolata:400,700|Work+Sans:300,400,500,600,700");
.header {
  background: #56CBF9;
  text-transform: uppercase;
  padding: 0.1em 0;
  width: 110vw;
  margin-left: -5vw;
  margin-bottom: 3em;
  transform: rotateZ(-3deg);
  overflow: hidden; }
  .header h1 {
    text-align: center;
    font-size: 5em;
    margin: 0; }
  .header h2 {
    font-weight: lighter;
    margin: 0.3em 0;
    white-space: nowrap;
    overflow: hidden; }
    .header h2 a {
      font-weight: lighter;
      margin: 0 1em;
      padding: 0 0.2em;
      color: #363636; }
    .header h2 a:hover, .header h2 a.active {
      color: #EDF4ED;
      background: #363636; }
  .header h2::before, .header h2::after {
    content: "";
    margin: 0.2em 0;
    position: relative;
    display: block;
    width: 100%;
    border-bottom: 1px solid #81F4E1; }



