@import url("https://fonts.googleapis.com/css?family=Inconsolata:400,700|Work+Sans:300,400,500,600,700");
.img-desc {
  text-align: center !important;
  margin: 0;
  color: #666; }

.headerwrapper {
  overflow: hidden; }

.justify-p p {
  text-align: justify; }

.main-container {
  min-height: 55vh;
  padding: 1em 25vw; }
  .main-container img {
    margin-left: auto;
    margin-right: auto;
    display: block;
    max-width: 100%; }

@media only screen and (max-device-width: 600px) {
  .main-container {
    padding: 1em 10px; } }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #EDF4ED; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #FF729F; }



